








































import { Vue, Component, Prop, Emit } from 'vue-property-decorator';
import { Itask } from '../store';
interface Itemtask {
  id: number;
  value: string;
  tasked: boolean;
}

type iPush = (item: Itemtask) => any;

interface Itasks {
  [index: number]: Itemtask;
  push: iPush;
  length: number;
}
type iChangDialog = (key: number) => void;
@Component
export default class List extends Vue {
  // @Prop(Array) tasks!:Itasks; //正确
  @Prop({
    type: Array,
    default: [
      {
        id: 0,
        value: '',
        tasked: false,
      },
    ],
    required: true,
  })
  public tasks!: Itasks;
  @Emit('changeDialog')
  public changeDialog(key: number) {
    return key;
  }
  public async addTask() {
    try {
      const newTask: Itask = await this.$store.dispatch('asyncAddtask', {
        id: this.tasks.length,
        value: '新任务',
        tasked: false,
      });
      
    } catch (Error) {
      console.error(Error);
    }
  }
}
